import React, { useState } from "react";
import LOGO from "../../../assets/filespin-io.png";
import { Button } from "antd";

const Header = ({ setAuth }) => {
  const [loading, setLoading] = useState(false);
  return (
    <div
      style={{
        height: "70px",
        minHeight: "70px",
        zIndex: 2,
        // backgroundColor: "#1677ff",
        backgroundColor: "rgba(0,0,0,0.9)",
        width: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
        borderTopLeftRadius: "0px",
        borderTopRightRadius: "0px",
      }}
    >
      <div
        className="body-holder"
        style={{
          fontSize: "30px",
          marginInline: "30px",
          color: "#fff",
          fontWeight: 600,
        }}
      >
        <span>Radio</span> <span style={{ color: "#1677ff" }}>Drive.</span>
      </div>

      <Button
        className="nBtn"
        size="large"
        loading={loading}
        type="primary"
        style={{
          marginRight: "30px",
          marginBottom: "3px",
          width: "110px",
        }}
        onClick={() => {
          setLoading(true);
          setTimeout(() => {
            setAuth(false);
            localStorage.setItem("auth", false);
            setLoading(false);
            window.location.replace("/");
          }, 2000);
        }}
      >
        <span style={{ marginLeft: "5px" }}>Logout</span>
      </Button>
    </div>
  );
};

export default Header;
