import { Button, Col, message, Modal, Row } from "antd";
import React, { useEffect, useState } from "react";
import {
  CloudUploadOutlined,
  SearchOutlined,
  DeleteOutlined,
  LoadingOutlined,
  SyncOutlined,
  CloseCircleOutlined,
} from "@ant-design/icons";
import axios from "../../utils/axios";
import Header from "./Header";
import { Spin } from "antd";

import "./index.css";
import Footer from "./Footer";

const Home = ({ setAuth }) => {
  const [loadingType, setLoadingType] = useState("all");
  const [loading, setLoading] = useState(false);

  const [images, setImages] = useState(null);
  const [filterImages, setFilterImages] = useState(null);
  const [idImageMap, setIdImageMap] = useState({});

  const [error, setError] = useState(false);
  const [errorMsg, setErrorMsg] = useState("Oops ! Something went wrong...");
  const [searching, setSearching] = useState(false);

  const [showModal, setShowModal] = useState(true);
  const [modalData, setModalData] = useState(null);

  const fetch = (id) => {
    return axios.get("/api/get_image?id=" + id);
  };

  const fetchImages = (list) => {
    setLoadingType("images");
    const map = {};
    Promise.all(list.map((u) => fetch(u)))
      .then((responses) =>
        Promise.all(
          responses.map((res) => {
            if (res.data.status === "OK") {
              map[res.data.name] = res.data.data;
              return { image: res.data.data, name: res.data.name };
            }
          })
        )
      )
      .then((images) => {
        setIdImageMap(map);
        setImages(images);
        setFilterImages(images);
      })
      .catch((err) => {
        console.log(err);
        setError(true);
        setErrorMsg("Oops ! Something went wrong...");
      });
  };

  const loadAllData = () => {
    axios
      .get("/api/get_image?id=all")
      .then((res, req) => {
        if (res.data.status === "OK") {
          if (res.data.data.length > 0) {
            fetchImages(res.data.data);
          }
        }
      })
      .catch((err) => {
        setError(true);
        setErrorMsg("Error loading data !. Please try again after sometime.");
      })
      .finally(() => {
        setTimeout(() => {
          setLoading(false);
        }, 2000);
      });
  };

  useEffect(() => {
    setLoading(true);
    loadAllData();
  }, []);

  const uploadImage = (selectedImage) => {
    let formData = new FormData();
    formData.append("file", selectedImage);
    axios
      .post("/api/upload", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((res, req) => {
        if (res.data["status"] === "OK") {
          message.success("Image Uploaded to Cloud !");
          loadAllData();
        }
      })
      .catch((err) => {
        const data = err.response.data;
        console.log(err);
        message.error(
          data["status"] ? data["status"] : "Oops ! Something went wrong."
        );
      });
  };

  const searchImage = (selectedImage) => {
    setSearching(true);
    let formData = new FormData();
    formData.append("file", selectedImage);
    axios
      .post("/api/search", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((res, req) => {
        if (res.data["status"] === "OK") {
          let fImages = [];
          Object.keys(res.data.data).forEach((key, index) => {
            fImages = [...res.data.data[key]];
          });
          let filImage = [];

          images.forEach((imgData, index) => {
            if (fImages.includes(imgData["name"].split(".jpg")[0])) {
              filImage.push(imgData);
            }
          });
          setTimeout(() => {
            message.success("Successfully Queried by Image !");
            setSearching(false);
            setFilterImages(filImage);
          }, 2000);
        }
      })
      .catch((err) => {
        const data = err.response.data;
        if (err.response.status == 404) {
          message.error("No History Available for Select Image !");
        } else {
          message.error(
            data["status"] ? data["status"] : "Oops ! Something went wrong."
          );
        }
        console.log(err);
        setTimeout(() => {
          setSearching(false);
        }, 2000);
      });
  };

  const UploadSelector = () => {
    return (
      <input
        id="file-select"
        type="file"
        name="myImage"
        onClick={onInputClick}
        style={{ display: "none" }}
        onChange={(event) => {
          console.log(event.target.files[0]);
          if (event.target.files[0]) {
            uploadImage(event.target.files[0]);
          }
        }}
      ></input>
    );
  };

  const SearchSelector = () => {
    return (
      <input
        id="file-search"
        type="file"
        name="myImage1"
        style={{ display: "none" }}
        onClick={onInputClick}
        onChange={(event) => {
          console.log(event.target.files[0]);
          if (event.target.files[0]) {
            searchImage(event.target.files[0]);
          }
        }}
      ></input>
    );
  };

  const onInputClick = (event) => {
    event.target.value = "";
  };

  const UploadButton = () => {
    return (
      <div
        className="wBtn"
        style={{
          position: "absolute",
          bottom: "60px",
          border: "none",
          backgroundColor: "#000",
          color: "#fff",
          padding: "10px",
          paddingInline: "20px",
          fontSize: "13px",
          display: "flex",
          cursor: "pointer",
          zIndex: 2,
        }}
        onClick={() => {
          document.getElementById("file-select").click();
        }}
      >
        <CloudUploadOutlined
          style={{ fontSize: "20px", marginRight: "10px", color: "#1677ff" }}
        />
        Upload Image
      </div>
    );
  };

  const SearchByImage = () => {
    return (
      <>
        <div
          style={{
            width: "100%",
            display: "flex",
            justifyContent: "center",
            zIndex: 2,
          }}
        >
          <div
            className="zBtn"
            style={{
              display: "flex",
              padding: "10px",
              width: "300px",
              marginTop: "30px",
              paddingInline: "30px",
              alignItems: "center",
              backgroundColor: "#e0e0e0",
              fontSize: "13px",
              cursor: "pointer",
              justifyContent: "center",
            }}
            onClick={() => {
              document.getElementById("file-search").click();
            }}
          >
            <SearchOutlined style={{ fontSize: "20px", marginRight: "10px" }} />{" "}
            Search by Image
          </div>

          <div
            className="zBtn"
            style={{
              display: "flex",
              padding: "10px",
              marginTop: "30px",
              paddingInline: "20px",
              alignItems: "center",
              backgroundColor: "#000",
              color: "#fff",
              fontSize: "13px",
              cursor: "pointer",
              justifyContent: "center",
              marginLeft: "30px",
            }}
            onClick={() => {
              setFilterImages(images);
            }}
          >
            <SyncOutlined
              style={{
                fontSize: "20px",
                marginRight: "10px",
                color: "#1677ff",
              }}
            />
            Reset Search
          </div>
        </div>
      </>
    );
  };

  const deleteImage = (id) => {
    axios
      .delete("/api/get_image?id=" + id)
      .then((res, req) => {
        if (res.data.status === "OK") {
          message.success(`${id} Deleted Successfully !`);
          setImages(images.filter((image, index) => image["name"] != id));
          setFilterImages(images.filter((image, index) => image["name"] != id));
          setShowModal(false);
          setModalData(null);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const ImageGrid = ({ imgSrc, name }) => {
    return (
      <div
        className="overlayFileName"
        style={{
          width: "200px",
          height: "200px",
          cursor: "pointer",
        }}
        onClick={() => {
          setModalData({ image: imgSrc, name: name });
          setShowModal(true);
        }}
      >
        <img
          src={imgSrc}
          alt="image"
          style={{
            backgroundColor: "yellow",
            borderRadius: "15px",
            width: "100%",
            height: "100%",
            objectFit: "cover",
            boxShadow: "0 0 8px rgba(0, 0, 0, 0.4)",
          }}
        />
        <div
          className="overlay del"
          onClick={() => {
            deleteImage(name);
          }}
        >
          <DeleteOutlined style={{ fontSize: "20px" }} />
        </div>
        <div className="overlay fn" style={{ fontSize: "11px" }}>
          <span style={{ padding: "3px", wordWrap: "break-word" }}>{name}</span>
        </div>
      </div>
    );
  };

  const Layout = ({ children }) => {
    return (
      <div
        style={{
          display: "flex",
          justifyContent: "flex-start",
          backgroundColor: "#e0e0e0",
          width: "100%",
          height: "100%",
          alignItems: "flex-start",
          flexDirection: "column",
          overflowX: "hidden",
        }}
      >
        {children}
      </div>
    );
  };

  const ImageLayout = ({ children }) => {
    return (
      <div
        style={{
          width: "100%",
          height: "100%",
          // margin: "50px",
          marginTop: "30px",
          padding: "20px",
        }}
      >
        {children}
      </div>
    );
  };

  const NoImage = () => {
    return (
      <>
        <div className="align-cent">
          <img
            style={{
              width: "200px",
              height: "200px",
              marginLeft: "20px",
            }}
            src={require("../../assets/no-image.png")}
          />
          No Images Uploaded. Yet ! 💙
        </div>
      </>
    );
  };

  const ImageView = () => {
    return (
      <Modal
        centered
        open={showModal}
        style={{
          overflow: "hidden",
          borderRadius: "20px",
        }}
        onCancel={() => {
          setShowModal(false);
          setModalData(null);
        }}
        okButtonProps={{ hidden: true }}
        cancelButtonProps={{ hidden: true }}
      >
        <div
          className="overlayFileName"
          style={{
            width: "100%",
            height: "100%",
            cursor: "pointer",
          }}
        >
          <img
            src={modalData["image"]}
            alt="n"
            style={{
              width: "100%",
              height: "100%",
              objectFit: "contain",
            }}
          />
          <div
            className="overlay del-modal"
            onClick={() => {
              deleteImage(modalData["name"]);
            }}
          >
            <DeleteOutlined style={{ fontSize: "20px" }} />
          </div>
          <div
            className="clo-modal"
            onClick={() => {
              setShowModal(false);
              setModalData(null);
            }}
          >
            <CloseCircleOutlined style={{ fontSize: "20px" }} />
          </div>
          <div className="overlay fn-modal" style={{ fontSize: "12px" }}>
            <span style={{ paddingInline: "20px", wordWrap: "break-word" }}>
              {modalData["name"]}
            </span>
          </div>
        </div>
      </Modal>
    );
  };

  const SearchingMod = () => {
    return (
      <div className="mo">
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            width: "300px",
            height: "200px",
            backgroundColor: "#fff",
            justifyContent: "center",
            borderRadius: "20px",
          }}
        >
          <Spin
            indicator={
              <LoadingOutlined
                className="tyu"
                style={{
                  fontSize: 78,
                  marginBottom: "30px",
                  color: "#1677ff",
                }}
                spin
              />
            }
          />
          Searching for relevant Images...
        </div>
      </div>
    );
  };

  return (
    <>
      <Layout>
        <Header setAuth={setAuth} />
        <SearchByImage />

        {searching && !error && (
          <>
            <SearchingMod />
          </>
        )}

        {!loading && !error ? (
          <>
            {filterImages ? (
              filterImages.length > 0 ? (
                <>
                  <ImageLayout>
                    <Row gutter={[50, 50]}>
                      {filterImages.map((image, index) => {
                        return (
                          <Col span={4}>
                            <ImageGrid
                              imgSrc={image["image"]}
                              name={image["name"]}
                            />
                          </Col>
                        );
                      })}
                    </Row>
                  </ImageLayout>
                </>
              ) : (
                <NoImage />
              )
            ) : (
              <NoImage />
            )}
          </>
        ) : (
          <>
            {!error && !searching ? (
              <div className="align-cent">
                <LoadingOutlined
                  style={{
                    fontSize: 78,
                    marginBottom: "30px",
                    color: "#1677ff",
                  }}
                  spin
                />
                {loadingType === "all"
                  ? "Fetching Data ! Please wait..."
                  : "Loading Images ! this might take sometime..."}
              </div>
            ) : (
              <>
                {!searching && error && !loading && (
                  <div className="align-cent">
                    <img
                      style={{
                        width: "300px",
                        height: "300px",
                      }}
                      src={require("../../assets/error.png")}
                    />
                    {errorMsg}
                  </div>
                )}
              </>
            )}
          </>
        )}
        <Footer />
      </Layout>
      <UploadSelector />
      <SearchSelector />
      <UploadButton />
      {modalData && modalData["image"] && showModal && <ImageView />}
    </>
  );
};

export default Home;
